"use client";

import { useRouter } from "next/navigation";

export default function DashboardPage() {
  const router = useRouter();

  return (
    <div>
      <h1 className="text-4xl font-bold text-gray-800 mb-8">Dashboard</h1>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {/* Book Appointment Card */}
        <div className="border rounded-xl p-6 flex flex-col justify-between space-y-4">
          <h2 className="text-2xl font-semibold">Book Appointment</h2>
          <button
            onClick={() => router.push("/appointments/book")}
            className="bg-blue-600 text-white py-2 px-4 rounded-lg hover:bg-blue-700 transition"
          >
            Book Appointment
          </button>
        </div>

        {/* My Appointments */}
        <div className="border rounded-xl p-6 flex items-center justify-between">
          <h2 className="text-2xl font-semibold">My Appointments</h2>
        </div>

        {/* Payment History */}
        <div className="border rounded-xl p-6 flex items-center justify-between">
          <h2 className="text-2xl font-semibold">Payment History</h2>
        </div>

        {/* Chat Support */}
        <div className="border rounded-xl p-6 flex items-center justify-between relative">
          <h2 className="text-2xl font-semibold">Chat Support</h2>
          <button
            onClick={() => router.push("/chat")}
            className="absolute bottom-4 right-4 bg-blue-600 text-white p-3 rounded-full hover:bg-blue-700"
          >
            ðŸ’¬
          </button>
        </div>
      </div>
    </div>
  );
}
